.advantages {
  color: $white;

  background: radial-gradient(50% 50% at 50% 50%, #0092F2 27.6%, #006BE9 100%);

  &__top {
    width: 144px;
  }

  &__bottom {
    width: 144px;
  }
}

.advantages__list {
  margin: 116px 0;
  padding: 0;

  list-style: none;

  li:first-child {
    padding-right: 60px;
  }

  li:last-child {
    padding-left: 75px;
  }

  li:not(:last-child) {
    position: relative;
    &::after {
      content: "";
      position: absolute;
      top: -49px;
      right: -17px;
      width: 1px;
      height: 270px;
      background-color: $white;
    }

    &:nth-child(1) {
      &::after {
        right: 15px;
      }
    }
  }
}

.advantages__item {
  p {
    margin: 0 auto;
    min-height: 42px;

    font-size: $largFontSize;
    font-weight: $boldFontWeight;
    line-height: $largLineHeght;
    text-transform: uppercase;
  }

  .advantages__middle {
    margin-top: 5px;
    margin-bottom: 8px;

    font-size: 64px;
    line-height: 75px;
    letter-spacing: -0.13em;

    &--normal {
      letter-spacing: normal;
    }
  }

  span {
    font-size: $largFontSize;
    font-weight: $boldFontWeight;
    line-height: $largLineHeght;
    letter-spacing: normal;
    text-transform: none;
  }
}
