.page-footer {
  margin: 129px 0 0;
  padding: 60px 0 28px;

  text-align: center;
  color: $white;

  background-color: $color-base;

  a {
    color: inherit;

    &:hover,
    &.active {
      color: #FFB90D;
    }
  }
}

.page-footer__list {
  margin: 26px 37px 28px 12px;

  list-style: none;

  li {
    margin-right: 19px;
  }

  a {
    font-size: $baseFontSize - 1;
    line-height: $baseLineHeght - 1;
  }
}

.page-footer__contacts {
  display: block;
  margin-bottom: 40px;

  font-size: 24px;
  font-weight: $mediumFontWeight;
  line-height: 28px;
}

.page-footer__copyright {
  font-size: $baseFontSize - 1;
  line-height: $baseLineHeght - 1;
}
