.structure {
  margin-top: 128px;

  font-size: 36px;
  font-weight: $baseFontWeight;
  line-height: 42px;
  letter-spacing: -0.05em;
}

.structure__header {
  margin-bottom: 133px;
}

.structure__title {
  margin-bottom: 43px;

  text-transform: uppercase;
}

.structure__view {
  color: $white;

  background: radial-gradient(50% 50% at 50% 50%, #0092F2 27.6%, #006BE9 100%);
}

.structure__pack {
  padding-top: 300px;
  min-height: 700px;

  font-size: 48px;
  font-weight: $boldFontWeight;
  line-height: 56px;
  letter-spacing: -0.02em;

  span {
    position: absolute;
    right: 193px;
    bottom: 219px;
    left: -191px;

    display: block;

    transform: rotate(-90deg);
  }
}

.structure__pack-image {
  position: absolute;
  top: -117px;
  left: -412px;
}

.structure__list {
  margin: 0;
  margin-left: 364px;
  padding: 30px 0;
  width: 234px;

  font-size: 24px;
  font-weight: $mediumFontWeight;
  line-height: 28px;
  text-transform: uppercase;

  list-style: none;
}

.structure__item {
  margin-top: 10px;
}
