.pack {
  color: $white;

  background: radial-gradient(50% 50% at 50% 50%, #0092F2 27.6%, #006BE9 100%);
}

.pack__wrapper {
  margin-bottom: 211px;

  background: url("../img/pack-map.png") no-repeat 90% 17px;

  &--header {
    height: 544px;
  }

  img {
    position: absolute;
    top: 45px;
    left: 274px;
  }
}

.pack__slogan {
  margin-top: 122px;
  margin-left: -17px;

  font-size: 65px;
  font-weight: $boldFontWeight;
  line-height: 75px;
  text-transform: uppercase;
  color: #00B107;

  span {
    display: inline-block;
    margin-top: 9px;
    padding: 2px 14px;

    letter-spacing: -0.03em;

    background-color: $white;
  }
}

.pack__text {
  margin-top: 42px;
  margin-left: -17px;
  max-width: 366px;

  font-size: $largFontSize;
  line-height: $largLineHeght;
  letter-spacing: -0.03em;
}
